.loginBackground {
	position           : absolute;
	background-color   : rgba(0, 0, 0, 0.472);
	width              : 100%;
	height             : 100%;
	left               : 50%;
	top                : 50%;
	transform          : translate(-50%, -50%);
	z-index            : 50;
	/* overflow        : inherit; */
}

.container {
	position : absolute;
	left     : 50%;
	top      : 50%;
	transform: translate(-50%, -50%);
	width    : 550px;
	top      : 50%
}

.inputBlock {
	padding : 20px;
	display : grid;
	grid-gap: 10px;
}

.inputBlock input {
	height          : 30px;
	border          : 1px solid #e6ecf5;
	border-radius   : 2px;
	background-color: white;
	color           : #515365;
	padding         : 0 10px;
	outline         : none;
	transition      : all .3s ease-in-out;
}

.sendBlock {
	display: flex;
}

.sendBlock :last-child {
	margin-left: 10px;
}